import { RES_URL } from "../utilis/constants";
import React from 'react';
const Restaurantcard=(props)=>{
    const{resData}=props;
    const{
        name,
        cuisines,
        avgRating,
        deliveryTime
    }=resData?.info;
    return(
    <div id="res-card">
    <div id ="img-logo"><img src={RES_URL+resData.info.cloudinaryImageId}/></div>
    <h3>{name}</h3>
    <h4>{cuisines.join(" , ")}</h4>
    <h4>{avgRating}</h4>
    <h4>{deliveryTime}</h4>
    </div>
    )
    }
export default Restaurantcard;    